import { Grid, Heading, HStack, Stack, Text } from "@chakra-ui/react"
import { Tabs, TabList, TabPanels, Tab, TabPanel } from '@chakra-ui/react'
import { ProjectDetails } from "../Components/Projects/ProjectDetails"

const projects = [
    { title: "Myntra", type: "E-commerce website", image: "/github/myntra.png", github: "https://github.com/Shibshankar01/Myntra", netlify: "https://grand-tarsier-842e30.netlify.app/", video: "https://www.linkedin.com/feed/update/urn:li:activity:6944263136359104512/" },
    { title: "Airtable", type: "Relational database sharing platform", image: "/github/airtable.jpg", github: "https://github.com/Kashif-Rezwi/glistening-cook-4365", netlify: "https://codeair.netlify.app/", video: "https://www.linkedin.com/feed/update/urn:li:activity:6997921662746652672/" },
]

export const Project = () => {

    return (
        <>
            <Stack w={"100%"} minH={"100vh"} boxSizing={"border-box"} justify={"center"} align={"center"}>

                <Heading pb={"30px"} as={"h1"}>Projects</Heading>

                <Tabs variant='soft-rounded' colorScheme='green' w={"100%"}>
                    <TabList display={"flex"} justifyContent={"center"} alignItems={"center"}>
                        <Tab>Sizable Projects</Tab>
                        <Tab isDisabled>Mini Projects</Tab>
                    </TabList>
                    <TabPanels w={"100%"}>
                        <TabPanel w={"100%"}>

                            <Grid templateColumns={{base:"repeat(1,1fr)", md:"repeat(2,1fr)", lg:"repeat(2,1fr)"}} w={"100%"} minH={"60vh"} m={"0"}>

                                {
                                    projects.map((item) => <ProjectDetails key={item.title} {...item} />)
                                }

                            </Grid>

                        </TabPanel>

                        <TabPanel>
                        <Grid templateColumns={{base:"repeat(1,1fr)", md:"repeat(2,1fr)", lg:"repeat(2,1fr)"}} w={"100%"} minH={"60vh"} m={"0"}>
                        </Grid>
                        </TabPanel>
                    </TabPanels>
                </Tabs>

            </Stack>
        </>
    )
}