import { Image, Text, VStack } from "@chakra-ui/react"

export const TechStack = ({name, icon}) => {

    const iconW = name === "Cypress"|| name === "HTML" ? "40px" : "50px"

    return (
        <VStack className="techStack" boxSizing={"border-box"} p={"5px"}
        w={{ base: "80px",sm: "90px", md: "100px", lg: "120px" }} 
        h={{ base: "80px",sm: "90px", md: "100px", lg: "120px" }} m={"auto"}>

            <Image w={{ base: name === "Cypress"|| name === "HTML" ? "30px" : "40px", sm: iconW, md: iconW, lg: iconW }} alt={name} src={icon} />
            <Text fontSize={{ base: "12px",sm: "13px", md: "14px", lg: "15px" }}>{name}</Text>

        </VStack>
    )
}