import { Box, HStack, Image, Link, Text, Stack } from "@chakra-ui/react"


export const ProjectDetails = ({ title, type, image, github, netlify, video }) => {
  return (
    <Stack flexDir={"column"} justify={"space-around"} align={"center"}  
    w={{base:"100%", sm:"300px", md:"300px",lg:"400px"}} 
    h={{base:"300px", sm:"250px", md:"250px",lg:"300px"}}
    m={"20px auto"} className={"projectDiv"} p={"10px 0px"} boxSizing={"border-box"}>

      <Box w={"90%"} textAlign={"left"}>
        <Text fontSize={"15px"} fontWeight={"600"}>{title}</Text>
        <Text fontSize={"14px"}>{type}</Text>
      </Box>

      <Box w={"90%"} className={"projectImg"}>
        <Image src={image} alt={title} />
      </Box>

      <HStack w={"90%"} textAlign={"left"}>

        <Link href={github}>
          <Image src={"/github/github.png"} w={"40px"} alt={"github"} />
        </Link>

        <Link href={netlify}>
          <Image src={"/github/netlify.png"} w={"35px"} alt={"netlify"} />
        </Link>

        <Link href={video}>
          <Image src={"/github/video.png"} w={"40px"} alt={"video"} />
        </Link>

      </HStack>

    </Stack>
  )
}