import { Grid, Heading, Image, Stack, Text, VStack } from "@chakra-ui/react"
import { TechStack } from "../Components/TechStack"

const techStack = [
    { icon: "/images/html5.png", name: "HTML" },
    { icon: "/images/css3.png", name: "CSS" },
    { icon: "/images/python.png", name: "Python" },
    { icon: "/images/javascript.png", name: "Javascript" },
    { icon: "/images/typescript.png", name: "Typescript" },
    { icon: "/images/nodeJs.png", name: "Node JS" },
    { icon: "/images/mongodb.png", name: "Mongodb" },
    { icon: "/images/react.png", name: "React JS" },
    { icon: "/images/nextJs.png", name: "Next JS" },
    { icon: "/images/redux.png", name: "Redux" },
    { icon: "/images/cypress.png", name: "Cypress" },
    { icon: "/images/chakraUi.png", name: "Chakra UI" },
    { icon: "/images/materialUi.png", name: "Material UI" },
    { icon: "/images/git.png", name: "Git" },
    { icon: "/images/firebase.png", name: "Firebase" }
]

export const Skills = () => {
    return (
        <>
            <Stack w={"100%"} h={"100vh"} boxSizing={"border-box"} justify={"center"} align={"center"}>

                <Heading pb={"30px"} as={"h1"}>Skills</Heading>

                <Grid w={"100%"} gap={"20px"}
                    templateColumns={{base: "repeat(3,1fr)", sm: "repeat(3,1fr)", md: "repeat(4,1fr)", lg: "repeat(5,1fr)"}}>

                    {
                        techStack.map((item) => <TechStack key={item.name} name={item.name} icon={item.icon} />)
                    }


                </Grid>


            </Stack>
        </>
    )
}