import { Box, Grid, Heading, Stack, Text, VStack } from "@chakra-ui/react"

export const About = () => {
    return (
        <>
            <Stack w={"100%"} h={"90vh"} boxSizing={"border-box"} justify={"center"} align={"center"}>

                <Heading pb={"10px"} as={"h1"}>About Me</Heading>

                <Grid w={"100%"} templateColumns={{ base: "repeat(1,1fr)", sm: "repeat(1,1fr)", md: "repeat(2,1fr)", lg: "repeat(2,1fr)" }}>

                    <Box textAlign={{base:"justify", sm:"justify", md:"right", lg:"right"}} w={"100%"} h={"100%"} p={{ base: "0px 15px",sm:"0px 15px" , md: "0px 8px 0px 0px", lg: "0px 10px 0px 0px" }}>

                            <Text m={"10px 0px"} fontSize={"30px"} fontWeight={"900"}>
                                Hi. my name is Kashif Rezwi, nice to meet you. Please take a moment to look around my portfolio.
                            </Text>

                    </Box>

                    <Box textAlign={{base:"justify", sm:"justify", md:"left", lg:"left"}} w={"100%"} h={"100%"} p={{ base: "0px 15px", sm:"0px 15px", md: "0px 0px 0px 8px", lg: "0px 0px 0px 10px" }}>

                            <Text m={"10px 0px"}>
                                A dedicated Full Stack Web Developer with 2000+ hours
                                of coding. Strong in design and integration with intuitive
                                problem-solving skills. Proficient in JavaScript, React,
                                Express, Node, and MongoDB. Passionate about
                                implementing and launching new projects, looking to start
                                my career as an entry-level Full Stack Web Developer
                                with a reputed firm driven by technology.
                            </Text>

                    </Box>

                </Grid>


            </Stack>

        </>
    )
}